export default {
  data: () => ({
    tags: [],
    load_tags: false,
    total_tags: 0,
    height: '200px'
  }),
  async mounted() {
    this.load_tags = true
    // await this.$axios.$get('/sanctum/csrf-cookie')
    const tags = await this.$axios.$get(`api/tags`)
    this.load_tags = false
    this.tags = tags.data
    this.total_tags = tags.total
  },
  async fetch() {}
}
